import $ from 'jquery';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
/* import 'bootswatch/darkly/bootstrap.min.css'; */
import './main.css';
import Chart from 'chart.js';

$(document).ready(function () {
    $('.progress').each(function (index) {
        let data = {
            datasets: [{
                data: [$('.progress-bar').attr('aria-valuenow'), 100 - $('.progress-bar').attr('aria-valuenow')],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(255, 255, 255)'
                ],
                borderColor: [
                    'rgba(255,99,132,1)'
                ],
                borderWidth: 1
            }]
        };

        // ici normand plutot le créer entièrement et le mettre au DOM en une seule fois
        $(this).replaceWith('<canvas id="graphique' + index + '" width="200" height="100"></canvas>');

        /* eslint-disable no-new */
        new Chart($('#graphique' + index), {
            type: 'doughnut',
            data: data,
            options: Chart.defaults.doughnut
        });
    });
});



